
.main *{
              font-family: Whitney, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
              font-weight: 400;
             
             }
             
             .main{
                 display: flex;
                 flex-wrap: wrap;
                 justify-content: space-between;
               text-align: left;
               gap:0px 40px;
               background-color: rgb(224, 234, 236);
               
             }
             .main>div{
                 width: max-content;
             }
             
             .main>div>p{
                 margin: 3px 0px;
                 font-size: 15px;
             }
             .main>div p:first-child{
             font-weight: 500;
             color:#33363f;
             margin: 20px 0px;
             font-size: 20px;
             }
             
             .mobile>div>div{
             display: flex;
             width: max-content;
             justify-content: space-between;
             }
             .mobile>div:first-child img{
                 width: 150px;
                 cursor: pointer;
             
             }
             .mobile>div:last-child>div{
                 width: 50%;
             
             }
             .mobile>div:last-child img{
                 width: 24px;
                 
             }
             
