.prc{
display: flex;
justify-content: space-between;
width: max-content;
align-items: center;
gap: 0px 5px;
}

.prc>P:first-child{
    font-size: 14px;
margin: 3px 0px;
font-weight: bold;
color: #393d45;


}
.prc>p:nth-child(2){
    font-size: 14px;
    font-weight: 400;
    color:#6f737b;
   text-decoration: line-through;

}
.prc>:last-child{
    color: #ff905a;
    font-size: 13px;


}

.wishlist{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    border: 1px solid rgb(185, 185, 185);
   width: 85%;
   margin:5px auto;
   color: rgb(33, 34, 41);
   animation: fadeIn .7s;

}
@keyframes fadeIn{
    0%{opacity: 0;}
    100%{opacity: 1;}
}
.wishlist:hover{
    border: 1px solid;
    cursor: pointer;
    z-index: 4;
}
.hoverWish{
    position: relative;
    background-color: rgba(255, 255, 255, 0.959);
    padding: 5px 0px;
    width: 100%;
   border-radius: 5px;
   position: relative;
   top: 1px;
}
.wishlistWord{
    font-weight: 400;

}

.size{ 
    font-size: 14px;
    margin:2px 0px ;
    animation: fadeIn .3s;



}
.size p{
    font-weight: 400;
    color: #53575f;
}